body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #e5e5e5;
}

/* hiding scrollbar */
::-webkit-scrollbar {
  display: none;
}

/* app container style */
.app-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-size: 30px;
  text-align: center;
}

/* media panel style */
.media-panel {
  padding: 10px;
  -ms-flex: 20% 1;
      flex: 20% 1;

  width: 340px;
  height: 980px;
  left: 0px;
  top: 0px;

  background: #ffffff;
  overflow: scroll;
}

/* hide scroll bar */
.media-panel::-webkit-scrollbar {
  display: none;
}

/* media panel text styles */
.media-text {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  text-align: left;
  color: #000;
}

/* global image style */
.images {
  width: 90%;
  height: 250px;
  margin: 3px;
  border: 1px solid grey;
  border-radius: 10px;
}
.images:active {
  width: 80%;
  height: 210px;
  margin: 50px 3px 0 50px;
  -webkit-box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
;
          box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
;
}
